    "code": "import React from 'react';\nimport PropTypes from 'prop-types';\nimport Button from '../Button';\nimport Text from '../Text';\nimport CrossIcon from '../Icon/Icons/Cross';\n\nimport {\n  BannerStyled,\n  BannerCloseButton,\n  Wrapper,\n  ButtonWrapper,\n} from './style';\n\nimport { orangeDark } from '../style/colors';\n\nexport default class Banner extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.state = {\n      isOpen: true,\n    };\n\n    this.closeBanner = this.closeBanner.bind(this);\n  }\n\n  closeBanner() {\n    this.setState({ isOpen: false });\n    const { onCloseBanner } = this.props;\n    if (onCloseBanner) {\n      onCloseBanner();\n    }\n  }\n\n  renderBannerContent(themeColor) {\n    const { customHTML, text, actionButton } = this.props;\n    if (customHTML) {\n      return (\n        <Wrapper>\n          {/* eslint-disable-next-line */}\n          <div dangerouslySetInnerHTML={customHTML} />\n        </Wrapper>\n      );\n    }\n    return (\n      <Wrapper>\n        <Text type=\"paragraph\" color=\"#FFF\">\n          {text}\n        </Text>\n        <ButtonWrapper>\n          <Button\n            type={themeColor === 'orange' ? 'orange' : 'primary'}\n            onClick={actionButton.action}\n            label={actionButton.label}\n            size=\"small\"\n          />\n        </ButtonWrapper>\n      </Wrapper>\n    );\n  }\n\n  render() {\n    const { isOpen } = this.state;\n    const { themeColor } = this.props;\n\n    if (isOpen) {\n      return (\n        <BannerStyled themeColor={themeColor}>\n          {this.renderBannerContent(themeColor)}\n          <BannerCloseButton>\n            <Button\n              type=\"text\"\n              icon={(\n                <CrossIcon\n                  color={themeColor === 'blue' ? '#fff' : orangeDark}\n                />\n              )}\n              hasIconOnly\n              onClick={this.closeBanner}\n              label=\"Close\"\n              size=\"small\"\n            />\n          </BannerCloseButton>\n        </BannerStyled>\n      );\n    }\n    return null;\n  }\n}\n\nBanner.propTypes = {\n  /** The main text of the banner */\n  text: PropTypes.string,\n\n  /** The text of the Call To Action of the banner */\n  actionButton: PropTypes.shape({\n    label: PropTypes.string,\n    action: PropTypes.func,\n  }),\n\n  /** Custom HTML */\n  customHTML: PropTypes.shape({ __html: PropTypes.string }),\n\n  /** Theme color. Can be `'blue'` or `'orange'` */\n  themeColor: PropTypes.oneOf(['blue', 'orange']),\n\n  /** Handler when the banner closes */\n  onCloseBanner: PropTypes.func,\n};\n\nBanner.defaultProps = {\n  text: '',\n  actionButton: {},\n  customHTML: null,\n  themeColor: 'blue',\n  onCloseBanner: null,\n};\n",
