    "code": "import React from 'react';\n\nimport PropTypes from 'prop-types';\nimport {\n  CrossSellStyled,\n  CrossSellHeader,\n  SubHeaderWrapper,\n  CardsContainer,\n  ButtonsContainer,\n  ButtonWrapper,\n  Footer,\n  Card,\n  TitleContainer,\n  CardImage\n} from './style';\nimport Text from '../Text/Text';\nimport Button from '../Button/Button';\n\nexport default class CrossSell extends React.Component {\n  renderHeader = () => {\n    const { texts } = this.props;\n\n    return (\n      <CrossSellHeader>\n        <Text type=\"h1\">{texts.title}</Text>\n        <SubHeaderWrapper>\n          <Text type=\"h3\">{texts.subtitle}</Text>\n        </SubHeaderWrapper>\n      </CrossSellHeader>\n    );\n  };\n\n  renderBody = () => {\n    const { texts } = this.props;\n\n    return (\n      <CardsContainer>\n        {texts.cards.map((card, idx) => (\n          <Card key={idx}>\n            <CardImage src={card.image} alt={card.alt} />\n            <TitleContainer>\n              <Text type=\"h3\">{card.title}</Text>\n            </TitleContainer>\n            <Text type=\"p\">{card.description}</Text>\n          </Card>\n        ))}\n      </CardsContainer>\n    );\n  };\n\n  renderFooter = () => {\n    const { button, priceTagline } = this.props.texts;\n\n    return (\n      <Footer>\n        <ButtonsContainer>\n          <ButtonWrapper>\n            <Button\n              type=\"primary\"\n              size=\"large\"\n              onClick={() => window.location.replace(button.url)}\n              label={button.label}\n              fullWidth\n            />\n          </ButtonWrapper>\n        </ButtonsContainer>\n        <Text type=\"p\">{priceTagline}</Text>\n      </Footer>\n    );\n  };\n\n  render() {\n    return (\n      <CrossSellStyled>\n        {this.renderHeader()}\n        {this.renderBody()}\n        {this.renderFooter()}\n      </CrossSellStyled>\n    );\n  }\n}\n\nCrossSell.propTypes = {\n  texts: PropTypes.shape({\n    title: PropTypes.string,\n    subtitle: PropTypes.string,\n    cards: PropTypes.arrayOf(\n      PropTypes.shape({\n        image: PropTypes.string,\n        alt: PropTypes.string,\n        title: PropTypes.string,\n        description: PropTypes.string,\n    })),\n    button: PropTypes.shape({\n      label: PropTypes.string,\n      url: PropTypes.string\n    }),\n    priceTagline: PropTypes.string,\n  }).isRequired,\n};",
