    "code": "import React from 'react';\nimport PropTypes from 'prop-types';\n\nimport { LinkStyled } from './style';\nimport { fontWeightMedium } from '../style/fonts';\n\nconst Link = ({ children, href, newTab, download, className, title, fontWeight }) => (\n  <LinkStyled\n    href={href}\n    target={newTab ? '_blank' : '_self'}\n    rel={newTab ? 'noopener noreferrer' : undefined}\n    download={download}\n    className={className}\n    title={title}\n    fontWeight={fontWeight}\n  >\n    {children}\n  </LinkStyled>\n);\n\nLink.propTypes = {\n  /** Linkable content. */\n  children: PropTypes.node.isRequired,\n  /** Url to navigate to. */\n  href: PropTypes.string.isRequired,\n  /** Opens new tab. */\n  newTab: PropTypes.bool,\n  /** Saves the linked URL instead of navigating to it. */\n  download: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),\n  /** Class passed by the DOM element. */\n  className: PropTypes.string,\n  /** Title of the link. */\n  title: PropTypes.string,\n  /** Font weight of the link. */\n  fontWeight: PropTypes.oneOf([500, 700]),\n};\n\nLink.defaultProps = {\n  newTab: false,\n  download: false,\n  className: undefined,\n  title: '',\n  fontWeight: fontWeightMedium,\n};\n\nexport default Link;\n",
