      "code": "import React, { useState } from 'react';\nimport Modal from '@bufferapp/ui/Modal';\nimport Button from '@bufferapp/ui/Button';\nimport Carousel from '@bufferapp/ui/Carousel';\n\n/** With Carousel Modal */\nexport default function ModalTest() {\n  const [modalOpen, openModal] = useState(false);\n  return (\n    <div style={{ width: '100%', height: '400px', position: 'relative' }}>\n      <Button\n        type=\"primary\"\n        onClick={() => {\n          openModal(true);\n        }}\n        label=\"Bring the modal!\"\n      />\n      {modalOpen && (\n        <Modal\n          width=\"1100px\"\n          closeButton={{callback: () => openModal(false)}}\n          noBackground\n          dismissible\n        >\n          <div>\n            <Carousel width=\"1000px\">\n              <div\n                style={{\n                  height: '500px',\n                  width: '1000px',\n                  background: '#cecece url(\"https://picsum.photos/id/1002/1000/500\")',\n                  display: 'flex',\n                  color: 'white',\n                  justifyContent: 'center',\n                  alignItems: 'center',\n                  fontSize: '100px',\n                  fontWeight: 500,\n                }}\n              >\n                oh\n              </div>\n              <div\n                style={{\n                  height: '500px',\n                  width: '1000px',\n                  background: '#cecece url(\"https://picsum.photos/id/1052/1000/500\")',\n                  display: 'flex',\n                  color: 'white',\n                  justifyContent: 'center',\n                  alignItems: 'center',\n                  fontSize: '100px',\n                  fontWeight: 500,\n                }}\n              >\n                hi\n              </div>\n              <div\n                style={{\n                  height: '500px',\n                  width: '1000px',\n                  background: '#cecece url(\"https://picsum.photos/id/1032/1000/500\")',\n                  display: 'flex',\n                  color: 'white',\n                  justifyContent: 'center',\n                  alignItems: 'center',\n                  fontSize: '100px',\n                  fontWeight: 500,\n                }}\n              >\n                there\n              </div>\n            </Carousel>\n          </div>\n        </Modal>\n      )}\n    </div>\n  );\n}\n",
