    "code": "import React from 'react';\nimport PropTypes from 'prop-types';\nimport styled from 'styled-components';\nimport { borderRadius } from '../style/borders';\nimport { fontSize } from '../style/fonts';\nimport Warning from '../Icon/Icons/Warning';\nimport Cross from '../Icon/Icons/Cross';\nimport { grayDark, grayLighter, grayDarker } from '../style/colors';\n\nconst colorMap = {\n  warning: {\n    border: '#a59638',\n    background: '#fdf8d8',\n    color: '#625920',\n  },\n  note: {\n    border: grayDark,\n    background: grayLighter,\n    color: grayDarker,\n  },\n};\n\nconst NoticeWrapper = styled.div`\n  border: ${props => `1px solid ${colorMap[props.type].border}`};\n  color: ${props => colorMap[props.type].color};\n  background: ${props => colorMap[props.type].background};\n  border-radius: ${borderRadius};\n  font-size: ${fontSize};\n  padding: 16px 16px;\n  display: flex;\n  justify-content: flex-start;\n  position: relative;\n`;\n\nconst WarningIcon = styled(Warning)`\n  display: block;\n  margin-right: 12px;\n  flex: 1 0 auto;\n  max-width: 16px;\n`;\n\nconst CloseButton = styled.button`\n  color: ${props => colorMap[props.type].color};\n  border: 0;\n  background: 0;\n  padding: 0;\n  margin: 0;\n  opacity: 0.8;\n  height: 16px;\n  position: absolute;\n  right: 16px;\n  &:hover {\n    color: ${props => colorMap[props.type].color};\n    opacity: 1;\n    cursor: pointer;\n  }\n`;\n\nfunction Notice({ children, dismiss, type }) {\n  return (\n    <NoticeWrapper type={type}>\n      {type === 'warning' && <WarningIcon />}\n      {children}\n      {dismiss && (\n        <CloseButton type={type} onClick={() => dismiss()}>\n          <Cross />\n        </CloseButton>\n      )}\n    </NoticeWrapper>\n  );\n}\n\nNotice.propTypes = {\n  children: PropTypes.node.isRequired,\n  dismiss: PropTypes.func,\n  /** can be warning, note */\n  type: PropTypes.string.isRequired,\n};\n\nNotice.defaultProps = {\n  dismiss: null,\n};\n\nexport default Notice;\n",
