      "code": "import React from 'react';\nimport Select from '@bufferapp/ui/Select';\n\n\n/** Basic Disabled */\nexport default function ExampleSelectDisabled() {\n  return (\n    <Select\n      onSelectClick={() => console.info('Main select clicked')}\n      label=\"Click Me\"\n      disabled\n      keyMap={{\n        id: '_id',\n        title: 'name',\n      }}\n      items={[\n        { _id: '1', name: 'Open' },\n        { _id: '2', name: 'Pending' },\n        { _id: '3', name: 'Closed' },\n      ]}\n    />\n  );\n}\n",
