      "methods": [],
      "code": "import React from 'react';\nimport Select from '@bufferapp/ui/Select';\n\n/** Multi-Select */\nexport default function ExampleSelect() {\n  return (\n    <Select\n      onSelectClick={() => console.info('Main select clicked')}\n      label=\"Click Me\"\n      multiSelect\n      items={[\n        { id: '1', title: 'Open', selected: true },\n        { id: '2', title: 'Pending' },\n        { id: '3', title: 'Closed' },\n      ]}\n      hideSearch\n    />\n  );\n}\n",
