    "code": "import React, { useRef, useEffect } from 'react';\nimport PropTypes from 'prop-types';\nimport styled, { keyframes } from 'styled-components';\nimport { Cross } from '../Icon';\nimport { white, red } from '../style/colors';\nimport { easeOutQuart } from '../style/animations';\n\nconst ESCAPE_KEY = 27;\nconst TAB_KEY = 9;\n\nconst fadeIn = keyframes`\n  0% {\n    opacity: 0;\n  }\n\n  100% {\n    opacity: 1;\n  }\n`;\n\nconst stagingAnimation = keyframes`\n  0% {\n    transform: scale(.5);\n    opacity: 0;\n  }\n\n  100% {\n    transform: scale(1);\n    opacity: 1;\n  }\n`;\n\nconst Container = styled.div`\n  background: rgba(0, 0, 0, 0.7);\n  display: flex;\n  height: 100%;\n  align-items: center;\n  position: fixed;\n  padding-top: 80px;\n  box-sizing: border-box;\n  width: 100%;\n  z-index: 9999;\n  flex-direction: column;\n  top: 0px;\n  left: 0px;\n\n  animation: 200ms ${fadeIn} ${easeOutQuart};\n`;\n\nconst Modal = styled.div`\n  background-color: ${white};\n  border-radius: 8px;\n  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.16);\n  box-sizing: border-box;\n  margin: 0 0 1rem;\n  padding: 16px;\n  width: fit-content;\n  overflow: hidden;\n  position: absolute;\n\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  outline: none;\n\n  animation: 300ms ${stagingAnimation} ${easeOutQuart};\n`;\n\nconst CloseButton = styled.button`\n  background: none;\n  border: none;\n  position: absolute;\n  top: 16px;\n  right: 16px;\n  cursor: pointer;\n  padding: 0;\n\n  transition: transform 0.15s ease-out;\n  svg {\n    transition: fill 0.15s ease-out;\n  }\n\n  &:hover {\n    transform: scale(1.1);\n    svg {\n      fill: ${red};\n    }\n  }\n`;\n\nconst SimpleModal = ({ children, closeAction }) => {\n  const modalRef = useRef(null);\n  const containerRef = useRef(null);\n\n  const handleTabKey = e => {\n    const focusableModalElements = modalRef.current.querySelectorAll(\n      'a[href], button, textarea, input[type=\"text\"], input[type=\"radio\"], input[type=\"checkbox\"], select, div[tabIndex=\"0\"]'\n    );\n    const firstElement = focusableModalElements[0];\n    const lastElement =\n      focusableModalElements[focusableModalElements.length - 1];\n\n    if (!e.shiftKey && document.activeElement === lastElement) {\n      firstElement.focus();\n      return e.preventDefault();\n    }\n\n    if (e.shiftKey && document.activeElement === firstElement) {\n      lastElement.focus();\n      e.preventDefault();\n    }\n  };\n\n  const keyListenersMap = new Map([\n    [ESCAPE_KEY, () => closeAction()],\n    [TAB_KEY, handleTabKey],\n  ]);\n\n  const clickToClose = e => {\n    if (e.target !== containerRef.current) return;\n    closeAction();\n  };\n\n  useEffect(() => {\n    function keyListener(e) {\n      const listener = keyListenersMap.get(e.keyCode);\n      return listener && listener(e);\n    }\n    document.addEventListener('keydown', keyListener);\n    containerRef.current.addEventListener('click', e => clickToClose(e));\n\n    return () => document.removeEventListener('keydown', keyListener);\n  }, []);\n\n  return (\n    <Container ref={containerRef} role=\"dialog\" aria-modal=\"true\">\n      <Modal\n        ref={modalRef}\n        tabIndex=\"0\" // this needs to have a tabIndex so that it can listen for the ESC key\n      >\n        <CloseButton\n          onClick={() => {\n            closeAction();\n          }}\n        >\n          <Cross />\n        </CloseButton>\n        {children}\n      </Modal>\n    </Container>\n  );\n};\n\nSimpleModal.propTypes = {\n  children: PropTypes.node.isRequired,\n  closeAction: PropTypes.func.isRequired,\n};\n\nexport default SimpleModal;\n",
    "level": 1,
    "id": "simplemodal",
    "parentName": "ui",
    "examples": [{
      "name": "SimpleModal",
      "description": "SimpleModal Example",
      "methods": [],
      "code": "import React, { useState } from 'react';\nimport SimpleModal from '@bufferapp/ui/SimpleModal';\nimport Text from '@bufferapp/ui/Text';\nimport Button from '@bufferapp/ui/Button';\n\n/** SimpleModal Example */\nexport default function ExampleSimpleModal() {\n  const [modalOpen, openModal] = useState(false);\n  return (\n    <div>\n      <Button\n        type=\"primary\"\n        onClick={() => {\n          openModal(true);\n        }}\n        label=\"Bring the modal!\"\n      />\n      {modalOpen && (\n        <SimpleModal closeAction={() => openModal(false)}>\n          <div style={{ width: '300px' }}>\n            <Text type=\"p\">\n              There is a theory which states that if ever anyone discovers\n              exactly what the Universe is for and why it is here, it will\n              instantly disappear and be replaced by something even more bizarre\n              and inexplicable.\n            </Text>\n            <button type=\"button\">merp</button>\n            <a href=\"#/merp\">booo</a>\n            <button type=\"button\">weeee</button>\n            {// eslint-disable-next-line\n              <div tabIndex=\"0\">Hello world i'm a focusable div</div>}\n          </div>\n        </SimpleModal>\n      )}\n    </div>\n  );\n}\n",
      "title": ""
    }]
